import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';
import MoreVertIcon from '@material-ui/icons/MoreVert';

const styles = theme => ({
    card: {
        maxWidth: 400,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    actions: {
        display: 'flex',
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
});



class Cards extends React.Component {
    state = { expanded: false };

    handleExpandClick = () => {
        this.setState(state => ({ expanded: !state.expanded }));
    };

    render() {
        const { classes, image, title, subheader, price, addItems } = this.props;

        return (
            <Card className={classes.card} style={{ maxWidth: '100%' }}>
                <CardHeader

                    action={
                        <IconButton aria-label="more">
                            <MoreVertIcon />
                        </IconButton>
                    }
                    title={title}
                    subheader={subheader}
                />
                <CardMedia
                    className={classes.media}
                    image={image}
                    title="Wine"
                    alt="Wine"
                    style={{ backgroundSize: "contain" }}
                />
                <CardContent>
                    <Typography component="p">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi varius tristique mi, id elementum enim pharetra vel. Ut scelerisque vel magna molestie tempus. Phasellus ac nulla accumsan, iaculis orci vitae, rutrum nulla. Nam ut turpis dui. Vestibulum dapibu
          </Typography>
                </CardContent>
                <CardActions className={classes.actions} disableActionSpacing>
                    <Typography variant="title" gutterBottom>
                        Rs {price}
                    </Typography>


                    <Fab
                        onClick={() => {
                            addItems({
                                image, title, subheader, price
                            })
                        }}
                        color="secondary" style={{ marginLeft: 'auto' }} aria-label="Add" className={classes.fab}>
                        <AddIcon />
                    </Fab>

                </CardActions>
            </Card>
        );
    }
}

Cards.propTypes = {
    classes: PropTypes.object.isRequired,
    image: PropTypes.string,
    avatar: PropTypes.string,
    addItems: PropTypes.func,
};

export default withStyles(styles)(Cards);