import React from 'react';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';

import { withStyles } from '@material-ui/core/styles';
import CardActions from '@material-ui/core/CardActions';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import { Button } from '@material-ui/core';

const styles = theme => ({
    card: {
        maxWidth: 400,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    actions: {
        display: 'flex',
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red,
    },
});
class Checkout extends React.Component {
    state = {

    };


    render() {

        const { itemsAdded } = this.props;
        const { classes, showCheckoutPage } = this.props;
        let Total = 0;
        const items = itemsAdded.map((i, index) => {

            const { title, count, subheader, image, price } = i;
            Total = Total + count * price;
            return <Grid key={index} item xs={12} md={6} lg={3}><Card className={classes.card} style={{ maxWidth: '100%' }}>
                <CardHeader
                    title={title}
                    subheader={subheader}
                />
                <CardMedia
                    className={classes.media}
                    image={image}
                    title="Wine"
                    alt="Wine"
                    style={{ backgroundSize: "contain" }}
                />
                <CardActions className={classes.actions} disableActionSpacing>

                    <Typography variant="title" gutterBottom>
                        {price} * {count} = {price * count} Rs
                    </Typography>

                </CardActions>
            </Card>
            </Grid>

        });

        return (
            <div>
                <Grid

                    container
                    spacing={16}
                    alignItems='flex-start'
                    direction='row'
                    justify='flex-start'
                    style={{ padding: 40 }}
                >

                    {items}
                    <Grid item xs={12} style={{ marginTop: 40, textAlign: 'right' }}>
                        <Button variant="contained" color="secondary" onClick={() => { showCheckoutPage(false) }}> Products </Button>

                        <Button variant="contained" style={{ marginLeft: 20 }} color="primary"> checkout </Button>
                        <Typography variant="title" gutterBottom>
                            Total :{Total} Rs
                    </Typography>
                    </Grid>
                </Grid>
            </div>
        );
    }
}
export default withStyles(styles)(Checkout);

