import React from 'react';
import Products from './Products';
import SignIn from './SignIn';
import Checkout from './Checkout';
class App extends React.Component {
    state = {
        showProducts: false,
        itemsAdded: [],
        checkout: false,
        count: 0
    };

    showProductsPage = () => {
        this.setState({ showProducts: true })
    }

    showCheckoutPage = (show) => {
        this.setState({ checkout: show })
    }
    addItems = (item) => {
        let { itemsAdded, count } = this.state;
        count = count + 1;
        let itemNotPresent = true;
        itemsAdded.forEach(i => {
            if (i.title === item.title) {
                i.count = i.count + 1;
                itemNotPresent = false;
            }
        });
        if (itemNotPresent) {
            item['count'] = 1;
            itemsAdded.push(item);
        }
        this.setState({
            itemsAdded,
            count
        });
    }
    render() {
        const { showProducts, itemsAdded, checkout, count } = this.state;
        return (
            <div>
                {showProducts
                    ? checkout ? <Checkout showCheckoutPage={this.showCheckoutPage} itemsAdded={itemsAdded} /> : <div><Products itemsAdded={itemsAdded} count={count} showCheckoutPage={this.showCheckoutPage} addItems={this.addItems} /></div>
                    : <div style={{ transform: 'translate(0%,50%)' }}>
                        <SignIn showProductsPage={this.showProductsPage} />
                    </div>}
            </div>

        );
    }
}

export default App;
